.chat-wrap {
  background: #FFFFFF;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.24);
  display: flex;
  flex-direction: row;
  /*height: 1055px;*/
  align-items: stretch;
}

.contacts-wrap {
  width: 240px;
  min-width: 240px;
  overflow: hidden;
}

.chat-block {
  flex-grow: 1;
  min-width: 680px;
  max-width: 680px;
}

.contacts-search {
  padding: 16px 16px 15px 16px;
}

.contacts-search input {
  width: 100%;
  border: 1px solid #DDE5EB;
  padding-left: 40px;
  background-image: url("https://i.onthe.io/shpzkl1petq0cmi2m.949e286a.png");
  background-repeat: no-repeat;
  background-position: 12px 50%;
}

.contact {
  overflow-y: auto;
  overflow-x: hidden;
  padding-bottom: 60px;
  position: relative;
  height: 982px;
}

.contact-user {
  padding: 16px 8px 16px 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
  transition: .2s all linear;
}

.contact-user:hover {
  background-color: #F5F5F5;
  transition: .2s all linear;
  text-decoration: none;
}

.contact-user.active {
  background-color: #F5F5F5;
}


.contact-user-foto.online:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0px;
  right: 0px;
  width: 6px;
  height: 6px;
  background-color: #04C01B;
  border-radius: 50px;
}

.contact-user-foto {
  display: block;
  border: 2px solid #F0FAFF;
  width: 40px;
  height: 40px;
  margin-right: 8px;
  border-radius: 100px;
  position: relative;
  background: #DDE5EB;
  overflow: hidden;
}

.contact-user-foto a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contact-user-info {
  flex-grow: 1;
}

.contact-user-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
  margin-bottom: 5px;
}

.contact-user-name {
  font-weight: 600;
  line-height: normal;
  font-size: 12px;
  color: #000000;
}

.contact-user-name:hover {
  text-decoration: none;
  color: #000000;
}

.contact-user-text {
  font-weight: 600;
  line-height: 21px;
  font-size: 12px;
  color: #8C8C8C;

  max-width: 150px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.contact-user-text.blocked {
  color: #FFAB00;
}

.chat-this-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px 16px;
}

.chat-this-foto {
  width: 56px;
  height: 56px;
  overflow: hidden;
  margin-right: 16px;
}

.chat-this-foto a {
  display: block;
  width: 56px;
  height: 56px;
  border: 2px solid #F0FAFF;
  border-radius: 100px;
  overflow: hidden;
  background: #D1D8DE;
}

.chat-this-foto img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.chat-this-info-right {
  flex: 1 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.chat-this-user-top {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 8px;
}

.chat-this-user-name,
.chat-this-user-name:hover {
  font-weight: 600;
  line-height: 16px;
  font-size: 16px;
  color: #000000;
  text-decoration: none;
  margin-right: 10px;
}

.chat-this-user-bot {
  font-weight: normal;
  line-height: 24px;
  font-size: 14px;
  color: #8C8C8C;
}

.chat-this-user-foto,
.chat-this-user-foto:hover {
  display: inline-block;
  margin-left: 16px;
  color: #8C8C8C;
  text-decoration: none;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  vertical-align: bottom;
  background-image: url(data:image/svg+xml;base64,);
  background-position: 0% 50%;
  background-repeat: no-repeat;
  padding-left: 25px;

}

.chat-this-user-btn {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.chat-this-user-btn .main-btn {
  margin-right: 8px;
  font-size: 12px;
}

.chat-this-user-btn select {
  width: 80px;
}

.chat-form .textarea-wrap textarea {
  height: 163px;
}

.chat-form .textarea-wrap {
  margin-bottom: 16px;
}

.chat-form .checkbox-wrap label {
  font-weight: 400;
}

.textarea-count-wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.chat-form-btn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 24px 16px 16px 16px;
  background: #FAFAFA;
}

.chat-form-btn .main-btn:last-child {
  margin-right: 0px;
}

.chat-cost-wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0px 16px 16px 16px;
  background-color: #FAFAFA;
}

.chat-cost-wrap > div {
  display: flex;
  flex-direction: row;
}

.chat-cost {
  margin-right: 16px;
  line-height: 24px;
  font-size: 14px;
  letter-spacing: 0.25px;
  color: #8C8C8C;
}

.chat-cost:last-child {
  margin-right: 0px;
}

.chat-cost-title {
  font-weight: bold;
  line-height: 23px;
  font-size: 14px;
}

.blocked-wrap {
  position: relative;
}

.blocked .blocked-wrap:after {
  content: '';
  display: block;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: 1;
  background: rgba(255, 255, 255, 0.94);
}

.blocked-text {
  display: none;
}

.blocked .blocked-text {
  display: block;
  position: absolute;
  z-index: 2;
  left: 0px;
  right: 0px;
  text-align: center;
  top: 32px;
  font-weight: normal;
  line-height: 29px;
  font-size: 18px;
  letter-spacing: 0.5px;
  color: #333333;
}

.blocked-text-title {
  font-weight: 600;
  line-height: 40px;
  font-size: 24px;
  color: #000000;
  display: block;
  margin-bottom: 8px;
}

.chat-this-user-bot-blocked {
  display: none;
}

.blocked .chat-this-user-bot-blocked {
  display: block;
  line-height: 24px;
  font-size: 14px;
  color: #F77705;
}

.blocked .chat-this-user-local,
.blocked .chat-this-user-foto {
  display: none;
}

.blocked .chat-this-user-btn button,
.blocked .chat-this-user-btn select {
  display: none;
}

.chat-this-user-btn button.blocked-btn {
  display: none;
}

.blocked .chat-this-user-btn button.blocked-btn {
  display: block;
  min-width: 137px;
  margin-right: 0px;
}

.chat-form {
  padding: 22px 16px 16px 16px;
}
