    display: inline-block;
    width: 16px;
    height: 16px;
    background: #F0FAFF;
    border: 1px solid #DDE5EB;
    box-shadow: inset 0px 2px 8px #DDE5EB;
    margin-right: 16px;
    transition: .2s all linear;
    background-repeat: no-repeat;
    background-position: 50% 50%;

}

