    pointer-events: none;
}

.checkbox-wrap.disabled label {
    color: #8C8C8C;
}

.checkbox-wrap.disabled label span {
    background: #F5F5F5;
    box-shadow: inset 0px 0px 0px #DDE5EB;

}

.checkbox-wrap.disabled input:checked ~ label span {
    border: 1px solid #DDE5EB;
    background: #F5F5F5;
    box-shadow: inset 0px 0px 0px #DDE5EB;
    background-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTQiIGhlaWdodD0iMTIiIHZpZXdCb3g9IjAgMCAxNCAxMiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48cGF0aCBkPSJNMTIgMkw1IDlMMiA2IiBzdHJva2U9IiNCRkJGQkYiIHN0cm9rZS13aWR0aD0iMyIvPjwvc3ZnPg==);
    background-repeat: no-repeat;
