.radio-wrap {
    position: relative;
}

.radio-wrap input {
    overflow: hidden;
    width: 0px;
    height: 0px;
    opacity: 0;
    position: absolute;
}

.radio-wrap label {
    cursor: pointer;
    font-weight: 600;
    line-height: 16px;
    font-size: 16px;
    color: #595959;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0px;
}

.radio-wrap label span {
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-right: 16px;
    background: #F0FAFF;
    border: 1px solid #DDE5EB;
    box-shadow: inset 0px 2px 8px #DDE5EB;
    border-radius: 50px;
    overflow: hidden;
    position: relative;
}

.radio-wrap label span:after {
    content: '';
    display: block;
    position: absolute;
    top: 4px;
    right: 4px;
    left: 4px;
    bottom: 4px;
    background-color: #04C01B;
    border-radius: 50px;
    transform: scale(0);
    transition: .2s all linear;
}

.radio-wrap input:checked ~ label span {
    background: #fff;
    box-shadow: inset 0px 0px 0px #DDE5EB;
    border: 1px solid #04C01B;
    transition: .2s all linear;
}

.radio-wrap input:checked ~ label span:after {
    transform: scale(1);
    transition: .2s all linear;
}

.radio-wrap.disabled {
    pointer-events: none;
}

.radio-wrap.disabled label span {
    background-color: #F5F5F5;
    box-shadow: none;
}

.radio-wrap.disabled input:checked ~ label span {
    background: #F5F5F5;
    box-shadow: none;
    border: 1px solid #DDE5EB;
    transition: .2s all linear;
}

.radio-wrap.disabled input:checked ~ label span:after {
    background: #BFBFBF;
}

/*end input*/

