.chat-notice {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 32px;
  height: 32px;
  text-align: center;
  padding: 4px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url(data:image/svg+xml;base64,);
  transition: .3s all linear;
}

.chat-notice:hover{
  background-color: rgba(255, 255, 255, .1);
  transition: .3s all linear;
}

.chat-notice-content {
  text-align: left;
  position: absolute;
  right: 0px;
  top: 100%;

  width: 500px;
  padding: 16px;
  background: #FFFFFF;
  border: 1px solid #F2F2F2;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
  border-radius: 4px;

  font-weight: normal;
  line-height: 23px;
  font-size: 14px;
  letter-spacing: 0.25px;
  color: #262626;
  mix-blend-mode: normal;
  display: none;
}

.chat-notice:hover .chat-notice-content {
  display: block;
}

.chat-modal {
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  z-index: 1;
  background: #FFFFFF;
  border: 1px solid #FAFAFA;
  display: none;
}

.chat-modal.active {
  display: block;
}

.chat-modal-head {
  padding: 16px;
  background-color: #FAFAFA;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.chat-modal-title {
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}

.chat-modal-close {
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
}

.chat-modal-close:focus {
  outline: none;
}

.chat-modal-content {
  height: 236px;
  padding: 8px;
  overflow: auto;
}

.stickers-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: -4px;
  margin-right: -4px;
  display: none;
}

.stickers-wrap.active {
  display: flex;
}

.stickers-wrap .stickers {
  border: 1px solid #F5F5F5;
  border-radius: 4px;
  width: 104px;
  height: 104px;
  margin-right: 4px;
  margin-left: 4px;
  margin-bottom: 8px;
  overflow: hidden;
  cursor: pointer;
}

.stickers-wrap .stickers img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.smile-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: -4px;
  margin-right: -4px;
  display: none;
}

.smile-wrap.active {
  display: flex;
}

.smile-wrap .smile {
  cursor: pointer;
  width: 24px;
  height: 24px;
  margin-bottom: 8px;
  margin-right: 4px;
  margin-left: 4px;
}

.smile-wrap .smile img {
  width: 100%;
  height: 100%;
  object-fit: contain;
