.chat {
  background: linear-gradient(0.08deg, #607D8B 0.09%, #90A4AE 99.95%);
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  max-width: 100%;
  height: 656px;
  padding: 36px 24px 24px 24px;
  position: relative;

  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-start;
  border-bottom: 2px solid #F2F2F2;
  border-top: 2px solid #F2F2F2;

}

.chat:after {
  content: '';
  display: block;
  height: 36px;
  width: 100%;
  min-height: 8px;
}

.chat-placeholder, .chat-empty {
  display: flex;
  height: 100%;
  color: #ffffff;
  align-items: center;
  justify-content: center;
}

.chat-message {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  align-items: flex-end;
  margin-top: 24px;
  padding-right: 56px;
  flex-wrap: wrap;
}

.chat-message-time {
  width: 100%;
  margin-top: 8px;
  font-weight: 600;
  line-height: 24px;
  font-size: 12px;
  letter-spacing: 0.25px;
  color: #FFFFFF;
  padding-left: 56px;
}

.chat-message-foto {
  min-width: 40px;
  width: 40px;
  height: 40px;
  border: 2px solid #F0FAFF;
  border-radius: 100px;
  overflow: hidden;
  background-color: #DDE5EB;
  margin-right: 16px;
}

.chat-message-foto img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.chat-message-text {
  max-width: 520px;
  width: calc(100% - 56px);
  padding: 16px;
  background: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.16), 0px 4px 6px rgba(0, 0, 0, 0.1);
  font-weight: normal;
  line-height: 24px;
  font-size: 16px;
  text-align: justify;
  letter-spacing: 0.25px;
  color: #000000;
  border-radius: 16px;
  border-bottom-left-radius: 0px;
}

.chat-message.user-message {
  justify-content: flex-start;
  flex-direction: row-reverse;
  padding-left: 56px;
  padding-right: 0px;
}

.user-message .chat-message-foto {
  margin-left: 16px;
  margin-right: 0px;
}

.user-message .chat-message-text {
  background: #2B3038;
  color: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.16), 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 0px;
  font-weight: 300;
}

.user-message .chat-message-time {
  padding-left: 0px;
  padding-right: 56px;
  text-align: right;
}

.chat-message-stickers {
  /*width: 72px;*/
  /*height: 72px;*/
  /*overflow: hidden;*/
}

.chat-message-stickers svg {
  /*width: 72px;*/
  /*height: 72px;*/
}

.chat-notice {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 32px;
  height: 32px;
  text-align: center;
  padding: 4px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url(data:image/svg+xml;base64,);
  transition: .3s all linear;
}

.chat-notice:hover{
  background-color: rgba(255, 255, 255, .1);
  transition: .3s all linear;
}

.chat-notice-content {
  text-align: left;
  position: absolute;
  right: 0px;
  top: 100%;

  width: 500px;
  padding: 16px;
  background: #FFFFFF;
  border: 1px solid #F2F2F2;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
  border-radius: 4px;

  font-weight: normal;
  line-height: 23px;
  font-size: 14px;
  letter-spacing: 0.25px;
  color: #262626;
  mix-blend-mode: normal;
  display: none;
}

.chat-notice:hover .chat-notice-content {
  display: block;
}

.chat-modal {
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  z-index: 1;
  background: #FFFFFF;
  border: 1px solid #FAFAFA;
  display: none;
}

.chat-modal.active {
  display: block;
}

.chat-modal-head {
  padding: 16px;
  background-color: #FAFAFA;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.chat-modal-title {
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}

.chat-modal-close {
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
}

.chat-modal-close:focus {
  outline: none;
}

.chat-modal-content {
  height: 236px;
  padding: 8px;
  overflow: auto;
}

.stickers-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: -4px;
  margin-right: -4px;
  display: none;
}

.stickers-wrap.active {
  display: flex;
}

.stickers-wrap .stickers {
  border: 1px solid #F5F5F5;
  border-radius: 4px;
  width: 104px;
  height: 104px;
  margin-right: 4px;
  margin-left: 4px;
  margin-bottom: 8px;
  overflow: hidden;
  cursor: pointer;
}

.stickers-wrap .stickers img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.smile-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: -4px;
  margin-right: -4px;
  display: none;
}

.smile-wrap.active {
  display: flex;
}

.smile-wrap .smile {
  cursor: pointer;
  width: 24px;
  height: 24px;
  margin-bottom: 8px;
  margin-right: 4px;
  margin-left: 4px;
}

.smile-wrap .smile img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
